import { Component, Inject, Input, OnInit, PLATFORM_ID } from '@angular/core';
import { Speaker } from '@app/shared/interfaces/speaker';
import { Session } from '@app/shared/interfaces/session';
import { StaticJsonDataService } from '@app/shared/services/static-json-data-service';
import { isPlatformBrowser } from '@angular/common';

@Component({
  selector: 'app-speaker',
  templateUrl: './speaker.component.html',
})
export class SpeakerComponent implements OnInit {
  constructor(
    @Inject(PLATFORM_ID) private platformId: Object,
    private staticJsonDataService: StaticJsonDataService
  ) {
    this.isBrowser = isPlatformBrowser(platformId);
  }

  @Input() speaker!:Speaker;

  isBrowser = false;
  jsonFile: string = 'allSessionizeData.json';
  sessions: Session[] = [];

  ngOnInit(): void {
    if (this.isBrowser) {
      this.setSessionsById();
    }
  }

  setSessionsById():void {
    this.staticJsonDataService
      .fetchJsonData(this.jsonFile)
      .then((jsonData:any) => {
        this.sessions = jsonData.sessions.filter((session:Session) => {
          const id = Number(session.id);

          return this.speaker.sessions.indexOf(id) !== -1
        });
      });
  }
}
